<template>
  <div class="swap">
    <el-tabs v-model="activeName" class="swap-tabs">
      <el-tab-pane label="Swap" name="Swap">
        <div class="swap-info PB-20">
          <div class="swap-top">
            <el-icon :size="30" class="swap-top__icon"><Histogram /></el-icon>
            <div class="swap-info__title MB-10">Swap</div>
            <div class="swap-info__title-info">Trade tokens in an instant</div>
            <el-icon :size="30" class="swap-top__icon right pointer"><Clock /></el-icon>
          </div>
          <div class="swap-item">
            <div class="swap-item__top flex-row-between">
              <div class="flex-row-start align-center pointer">
                <div class="flex-row-start align-center" @click="openTokensDialog('item1')">
                  <img v-lazy="'https://assets-cdn.trustwallet.com/blockchains/smartchain/assets/'+item1.address+'/logo.png'" class="swap-item__top-icon MR-10"/>
                  <div class="swap-item__top-name text-overflow__1">{{item1.symbol}}</div>
                  
                  <el-icon class="ML-5">
                    <ArrowDown />
                  </el-icon>
                </div>
                <el-icon class="ML-5" @click="copyHandler(item1.address)"><CopyDocument /></el-icon>
              </div>
              <div v-if="isLogin">
                <el-icon class="is-loading" v-if="item1.loading">
                  <Loading />
                </el-icon>
                <template v-else-if="item1.balance">
                  Balance：{{getBit(utils.formatUnits(item1.balance, item1.decimals), 5) }}
                </template>
              </div>
            </div>
            <div class="swap-item__input MT-10 flex-row-start align-center" :class="{'is-disabled': !isLogin}">
              <input :disabled="!isLogin || approveLoading" class="text-right flex-1" @input="price1Change('item1')" v-model="item1.price" placeholder="0.00">
              <el-tag class="ML-10 ml-2 pointer" effect="light" v-if="isLogin" @click="maxHandler" type="warning" size="small">MAX</el-tag>
            </div>
          </div>
          <img src="@/assets/images/transform.png" class="swap-transform pointer" @click="transformHandler" />
          <div class="swap-item">
            <div class="swap-item__top flex-row-between">
              <div class="flex-row-start align-center pointer">
                <div class="flex-row-start align-center" @click="openTokensDialog('item2')">
                  <img v-lazy="'https://assets-cdn.trustwallet.com/blockchains/smartchain/assets/'+item2.address+'/logo.png'" class="swap-item__top-icon MR-10"/>
                  <div class="swap-item__top-name text-overflow__1">{{item2.symbol}}</div>
                  <el-icon class="ML-5">
                    <ArrowDown />
                  </el-icon>
                </div>
                <el-icon class="ML-5" @click="copyHandler(item2.address)"><CopyDocument /></el-icon>
                <img class="ML-5 swap-item__top-wallet" v-if="isLogin && connectState == 1" src="@/assets/images/metamask.png" @click="addTokenHandler"/>
              </div>
              <div v-if="isLogin">
                <el-icon class="is-loading" v-if="item2.loading">
                  <Loading />
                </el-icon>
                <template v-else-if="item2.balance">
                  Balance：{{getBit(utils.formatUnits(item2.balance, item2.decimals), 5)}}
                </template>
              </div>
            </div>
            <div class="swap-item__input MT-10 flex-row-start align-center" :class="{'is-disabled': !isLogin}">
              <input :disabled="!isLogin || approveLoading" v-model="item2.price" class="text-right flex-1" @input="price1Change('item2')" placeholder="0.00">
            </div>
          </div>
          <div class="swap-slippage PL-30 PR-30 MT-10">
            
            <div class="swap-slippage__select flex-row-between pointer">
              <div class="text-16">GWEI</div>
              <div class="flex-row-end">
                <el-tag 
                  class="swap-slippage__select-tag" 
                  type="warning" 
                  :effect="gWei == item ? 'dark' : 'light'" 
                  @click="toggleGwei(item)"
                  v-for="item in gWeiList"
                  :key="item">
                  {{ item }}
                </el-tag>
                <input type="number" class="swap-slippage__select-input" placeholder="5" v-model.number="gWei">
              </div>
            </div>
          </div>
          <div class="PL-30 PR-30 MT-30">
            <div class="flex-row-between MB-10">
              <div class="text-yellow">Slippage Tolerance</div>
              <div class="text-blue">{{progress}}%</div>
            </div>
            <div>
              <el-slider v-model="progress" :min="0.1" :max="99" @input="progressChange"/>
              <div class="flex-row-between text-yellow">
                <div>0%</div>
                <div>100%</div>
              </div>
            </div>
          </div>
          <el-button v-if="!isLogin" class="swap-btn MT-30" @click="loginFn">Connect Wallet</el-button>
          <el-button v-else-if="!hasApprove" :loading="approveLoading" class="swap-btn MT-30" @click="approveHandler">Approving</el-button>
          <template v-else>
            <el-button v-if="!item1.price" disabled class="swap-btn MT-30">Enter an amount</el-button>
            <el-button v-else class="swap-btn MT-30" :loading="swapLoading" @click="swapSubmit">Swap</el-button>
          </template>
          <div class="flex-row-center">
            <el-checkbox v-model="professional" label="Professional" size="large" />
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="Limit" name="Limit">Limit</el-tab-pane>
      <el-tab-pane label="Liquidity" name="Liquidity">Liquidity</el-tab-pane>
      <el-tab-pane label="Perpetual" name="Perpetual">Perpetual</el-tab-pane>
    </el-tabs>
  </div>

  <div class="network-list">
    <template v-for="(item, index) in networkList" :key="item.ChainID">
      <div class="network-list__title text-center">{{item.Name}}</div>
      <div 
        class="network-list__item" 
        :class="{
          'active': currentNetwork.ChainID === item.ChainID && currentDex.address === sub.address
        }" 
        v-for="(sub, i) in item.Dex" 
        :key="sub.address"
        @click="toggleDex(index, i)">
        {{sub.symbol}}
      </div>
    </template>
  </div>

  <Tokens ref="tokens" @change="tokenChange" @update="tokenUpdate"/>
  <SwapConfirm ref="swapConfirm" :confirm="swapHandler" :amountOutMin="getBit(utils.formatUnits(amountOutMin.toString(), item2.decimals), 5)"/>
</template>

<script>
import Tokens from './modal/tokens.vue'
import SwapConfirm from './modal/swapConfirm.vue'
import copy from 'copy-text-to-clipboard'
import { ElNotification } from 'element-plus'
import { utils, BigNumber } from 'ethers'
import { getBit, Tips } from '../../libs/utils'
import { mapGetters, mapActions } from 'vuex'
import networkList from '@/api/abis/network.json'
let path = [];
let addressPath = [];
export default {
  data() {
    return {
      utils,
      getBit,
      // 是否开启专家模式
      professional: false,
      // 交易中loading
      swapLoading: false,
      // 授权中loading
      approveLoading: false,
      // 当前是否有授权
      hasApprove: false,
      activeName: 'Swap',
      progress: 10,
      // 是否登录
      isLogin: false,
      gWei: 5,
      gWeiList: [5, 7, 10, 15, 20],
      // 交换记录
      swapHistory: [],
      type: '',
      timer: null,
      balanceTimer: null,
      // 登录类型
      connectState: '',
      // 第一个币种信息
      item1: {
        name: "BNB",
        symbol: "BNB",
        address: "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c",
        balance: '',
        price: '',
        loading: false
      },
      // 第二个币种信息
      item2: {
        name: "BUSD",
        symbol: "BUSD",
        address: "0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56",
        balance: '',
        price: '',
        loading: false
      }
    }
  },
  components: {
    Tokens,
    SwapConfirm
  },
  watch: {
    'currentNetwork.ChainID': {
      immediate: true,
      handler() {
        const { Token } = this.currentNetwork
        const item1 = Token[0]
        const item2 = Token[1] || Token[0]
        this.item1 = {...this.item1, ...item1}
        this.item2 = {...this.item2, ...item2}
        this.gWei = 5 * this.currentNetwork.GWEI
        this.gWeiList = [
          5 * this.currentNetwork.GWEI, 
          7 * this.currentNetwork.GWEI, 
          10 * this.currentNetwork.GWEI, 
          15 * this.currentNetwork.GWEI, 
          20 * this.currentNetwork.GWEI
        ]
      }
    }
  },
  computed: {
    ...mapGetters(['currentNetwork', 'networkList', 'currentDex']),
    amountOutMin() {
      if (!this.item2.price) {
        return BigNumber.from(0)
      }
      let price = utils.parseUnits(this.item2.price.toString(), this.item2.decimals)
      let price1 = utils.parseUnits(this.item2.price.toString(), this.item2.decimals)
      let progress = utils.parseUnits((this.progress).toString(), this.item2.decimals)
      let num100 = utils.parseUnits('100', this.item2.decimals)
      return price1.sub(price.mul(progress).div(num100))
    }
  },
  beforeMount() {
    this.swapHistory = localStorage.getItem('swapHistory') ? JSON.parse(localStorage.getItem('swapHistory')): []
    this.type = this.$route.query.outputCurrency ? 'item2' : 'item1'
    const key = this.$route.params.key || networkList[0].Dex[0].symbol
    let dexIndex;
    const netWorkIndex = networkList.findIndex(item => {
      dexIndex = item.Dex.findIndex(sub => {
        return sub.symbol === key
      })
      if (dexIndex >= 0) {
        return item
      }
    })
    this.setCurrentDex({
      netWorkIndex,
      dexIndex
    })
  },
  mounted() {
    const progress = localStorage.getItem('progress')
    this.progress = progress ? JSON.parse(progress) : 10
    this.isLogin = window.wallet.accounts[0] ? true : false
    this.isLogin && this.loginCallBack()
    window.wallet.on('accountChange', () => {
      this.isLogin = window.wallet.accounts[0] ? true : false
      if (!this.isLogin) {
        this.clearTimer()
      }
      this.isLogin && this.loginCallBack()
    })
  },
  beforeUnmount () {
    this.clearTimer()
  },
  methods: {
    ...mapActions(['setCurrentDex', 'setConnectDialog']),
    clearTimer() {
      clearInterval(this.timer)
      clearTimeout(this.balanceTimer)
      this.timer = null 
      this.balanceTimer = null
    },
    // 切换dex
    toggleDex(netWorkIndex, dexIndex) {
      window.wallet.switchToOtherNetwork(this.networkList[netWorkIndex], this.networkList[netWorkIndex].Dex[dexIndex]).then(() => {
        this.setCurrentDex({
          netWorkIndex, 
          dexIndex
        })
        window.location.href = window.location.origin + `#/swap/${this.networkList[netWorkIndex].Dex[dexIndex].symbol}`
        window.location.reload()
      })
    },
    // 添加代币到钱包
    addTokenHandler() {
      const { symbol, address } = this.item2
      window.wallet.addTokenToMeta(address, symbol, 'https://assets-cdn.trustwallet.com/blockchains/smartchain/assets/'+address+'/logo.png');
    },
    // 记录用户滑点
    progressChange() {
      localStorage.setItem('progress', this.progress)
    },
    // 切换上下币种
    transformHandler(){
      const res = JSON.parse(JSON.stringify(this.item1))
      this.item1 = JSON.parse(JSON.stringify(this.item2))
      this.item2 = res
    },
    maxHandler() {
      this.item1.price = utils.formatUnits(this.item1.balance, this.item1.decimals)
      this.price1Change('item1')
    },
    // 交换
    async swapHandler () {
      let promise;
      const payableAmount = utils.parseUnits(this.item1.price.toString(), this.item1.decimals)
      const amountOutMin = this.amountOutMin
      const to = window.wallet.accounts[0]
      const deadline = utils.parseUnits((new Date().getTime() + 15 * 60 * 1000).toString(), this.item1.decimals)
      console.log('payableAmount:', payableAmount)
      console.log('amountOutMin:', amountOutMin)
      console.log('to:', to)
      console.log('path:', path)
      console.log('deadline:', deadline)
      // 主网币换其他币
      if (this.item1.address == this.currentNetwork.MainCoin.address) {
        const gaslimit = await window.wallet.ContractWETH.estimateGas[this.currentNetwork.MainCoin_TO_OTHER](
          amountOutMin,
          path,
          to,
          deadline,
          {
            value: payableAmount,
            gasPrice: utils.parseUnits(this.gWei.toString(), 9),
          }
        )
        promise = window.wallet.ContractWETH[this.currentNetwork.MainCoin_TO_OTHER](
          amountOutMin,
          path,
          to,
          deadline,
          {
            value: payableAmount,
            gasPrice: utils.parseUnits(this.gWei.toString(), 9),
            gasLimit: gaslimit.mul(120).div(100)
          }
        )
      } else if(this.item2.address == this.currentNetwork.MainCoin.address){
        const gaslimit = await window.wallet.ContractWETH.estimateGas[this.currentNetwork.OTHER_TO_MainCoin](
          payableAmount,
          amountOutMin,
          path,
          to,
          deadline,
          {
            gasPrice: utils.parseUnits(this.gWei.toString(), 9),
          }
        )
        // 其他币换主网币
        promise = window.wallet.ContractWETH[this.currentNetwork.OTHER_TO_MainCoin](
          payableAmount,
          amountOutMin,
          path,
          to,
          deadline,
          {
            gasPrice: utils.parseUnits(this.gWei.toString(), 9),
            gasLimit: gaslimit.mul(120).div(100)
          }
        )
      } else {
        const gaslimit = await window.wallet.ContractWETH.estimateGas.swapExactTokensForTokensSupportingFeeOnTransferTokens(
          payableAmount,
          amountOutMin,
          path,
          to,
          deadline,
          {
            gasPrice: utils.parseUnits(this.gWei.toString(), 9),
          }
        )
        // 其他币之间互换
        promise = window.wallet.ContractWETH.swapExactTokensForTokensSupportingFeeOnTransferTokens(
          payableAmount,
          amountOutMin,
          path,
          to,
          deadline,
          {
            gasPrice: utils.parseUnits(this.gWei.toString(), 9),
            gasLimit: gaslimit.mul(120).div(100)
          }
        )
      }
      
      this.swapLoading = true
      try {
        clearInterval(this.timer)
        const tx = await promise
        
        await tx.wait().then(data => {
          this.swapHistory.push({
            item1: JSON.parse(JSON.stringify(this.item1)),
            item2: JSON.parse(JSON.stringify(this.item2)),
            tx: JSON.parse(JSON.stringify(tx)),
            status: 1
          })
        }).catch(err => {
          this.swapHistory.push({
            item1: JSON.parse(JSON.stringify(this.item1)),
            item2: JSON.parse(JSON.stringify(this.item2)),
            tx: JSON.parse(JSON.stringify(tx)),
            status: 0
          })
        })
        ElNotification({
          type: 'success',
          title: 'success',
          message: 'Successful trade'
        })
        this.loginCallBack()
      } catch (error) {
        Tips(error)
      }
      
      localStorage.setItem('swapHistory', JSON.stringify(this.swapHistory))
      this.swapLoading = false
    },
    // 上面价格变化
    async price1Change(key) {
      let otherKey = key == 'item1' ? 'item2' : 'item1'
      this[key].price = this[key].price.replace(/[^0-9.]/g,'')
      if (this[key].price == '') {
        this[key].price = ''
        this[otherKey].price = ''
        return
      }

      clearInterval(this.timer)
      this.timer = setInterval(() => {
        this.price1Change(key)
      }, 3000)
      
      const price = this[key].price.toString()
      if (price === '0.') {
        return
      }
      let promises = [window.wallet.ContractWETH.getAmountsOut(utils.parseUnits(price, this[key].decimals), [
        this[key].address,
        this[otherKey].address
      ])]
      addressPath = [[this[key].address,this[otherKey].address]]
      this.currentNetwork.TransferTokens.forEach(item => {
        if (item.address != this[key].address && item.address != this[otherKey].address) {
          addressPath.push([this[key].address,item.address,this[otherKey].address])
          promises.push(window.wallet.ContractWETH.getAmountsOut(utils.parseUnits(price, this[key].decimals), [
            this[key].address,
            item.address,
            this[otherKey].address
          ]))
        }
      })
      Promise.allSettled(promises).then(data => {
          let result = '';
          let lastIndex;
          data.forEach((item, index) => {
            const {status, value} = item
            if (status == "fulfilled") {
              const last = value[value.length - 1]
              if (!result) {
                result = last
                lastIndex = index
              } else if (last.gt(result)) {
                result = last
                lastIndex = index
              }
            }
          })
          if (result && this[key].price && this[key].price != '0') {
            console.log(lastIndex, '最多可以换'+utils.formatUnits(result.toString(), this[otherKey].decimals)+'-----------'+result.toString())
            this[otherKey].price = utils.formatUnits(result, this[otherKey].decimals)
            path = addressPath[lastIndex]
          }
      })
    },
    async loginCallBack(loading = true) {
      clearTimeout(this.balanceTimer)
      this.item1.loading = loading
      this.item2.loading = loading
      this.connectState = window.wallet.connectState
      this.getItemBalance(this.item1.address, 'item1').then(({balance, decimals}) => {
        this.item1.balance = balance
        this.item1.decimals = decimals
        this.item1.loading = false
      })
      this.getItemBalance(this.item2.address, 'item2').then(({balance, decimals}) => {
        this.item2.balance = balance
        this.item2.decimals = decimals
        this.item2.loading = false
      })
      this.balanceTimer = setTimeout(() => {
        this.loginCallBack(false)
      }, 6000)
    },
    // 获取第一个币种余额
    async getItemBalance(address, keyType) {
      let balance;
      const contract = window.wallet.initContract(address)
      const decimals = await contract.decimals()
      if (address == this.currentNetwork.MainCoin.address) {
        this.hasApprove = true
        balance = await window.wallet.provider.getBalance()
      } else {
        if (keyType == 'item1') {
          this.approveLoading = true
          this.hasApprove = false
          try {
            const data = await contract.allowance(window.wallet.accounts[0], this.currentDex.address)
            const res = Math.floor(Number(utils.formatUnits(data, decimals)))
            if (res <= 0) {
              this.hasApprove = false
              // 钱包模式下自动授权
              if (this.connectState == 3) {
                await this.approveHandler()
              }
            } else {
              this.hasApprove = true
            }
          } catch (error) {
            console.log(error)
            // Tips(error)
          }
          this.approveLoading = false
        }
        balance = await contract.balanceOf(window.wallet.accounts[0])
      }
      return {
        balance,
        decimals
      }
    },
    // 授权
    async approveHandler() {
      this.approveLoading = true
      try {
        const contract = window.wallet.initContract(this.item1.address)
        const tx = await contract.approve(
          this.currentDex.address,
          utils.parseEther('10000000000000'),
          {gasPrice: utils.parseUnits('10', 9)}
        )
        await tx.wait();
        this.hasApprove = true
      } catch (error) {
        Tips(error)
      }
      this.approveLoading = false
    },
    // 更新币种信息
    tokenUpdate({address, name, symbol}) {
      if (this.item1.address == address) {
        this.item1.name = name
        this.item1.symbol = symbol
      } else if (this.item2.address == address) {
        this.item2.name = name
        this.item2.symbol = symbol
      }
    },
    // 修改需要转换的币
    async tokenChange(data) {
      if (this.type == 'item1') {
        if (this.item2.address == data.address) {
          this.item2 = JSON.parse(JSON.stringify(this.item1))
        }
      } else {
        if (this.item1.address == data.address) {
          if (this.item1.address == data.address) {
            this.item1 = JSON.parse(JSON.stringify(this.item2))
          }
        }
      }
      data.price = data.price || '0'
      this[this.type] = data
      this.isLogin && this.loginCallBack()
      this.price1Change(this.type == 'item1' ? 'item2' : 'item1')
    },
    copyHandler(text) {
      copy(text)
      ElNotification({
        type: 'success',
        title: 'success',
        message: 'The contract address has been copied to your clipboard'
      })
    },
    toggleGwei(data) {
      this.gWei = data
    },
    openTokensDialog(type) {
      this.type = type
      this.$refs.tokens.open([this.item1.address, this.item2.address,])
    },
    swapSubmit() {
      if (this.professional) {
        this.swapHandler()
      } else {
        this.$refs.swapConfirm.open(this.item1, this.item2)
      }
    },
    // 连接钱包
    loginFn() {
      this.setConnectDialog(true)
    }
  }
}
</script>

<style lang="scss">
.network-list{
  position: fixed;
  top: 50px;
  right: 0;
  width: 300px;
  height: 100%;
  background: #fff;
  color: #333;
  .network-list__title{
    font-size: .18rem;
    line-height: 1rem;
    color: #999;
  }

  .network-list__item{
    font-size: .24rem;
    line-height: 1rem;
    text-align: center;
    background: #FCBE2F;
    &.active{
      background: #898989;
    }
  }
}
.el-checkbox__input.is-checked .el-checkbox__inner{
  background: rgba(252, 190, 47, 1);
  border-color: rgba(252, 190, 47, 1);
}
.el-checkbox__input.is-checked+.el-checkbox__label{
  color: rgba(252, 190, 47, 1);
}
.el-slider__bar{
  background: rgba(252, 190, 47, 1);
}
.el-slider__button{
  border-color: rgba(252, 190, 47, 1);
}
.swap-slippage__select-input{
  width: 40px;
  height: 20px;
  background: none;
  border-radius: 4px;
  border: 1px solid #898989;
  text-align: center;
  color: #fff;
}
.swap-slippage__select-tag{
  height: 20px;
  line-height: 18px;
  margin-right: 10px;
}
  .swap-tabs{
    margin: 0 auto;
    width: 410px;
    .el-tabs__item{
      color: #fff;
    }
    .el-tabs__nav-wrap::after{
      display: none;
    }
    .el-tabs__active-bar{
      background: #fff;
    }
  }
  .swap-info{
    width: 410px;
    border: 1px solid #2A2A2A;
    border-radius: 18px;
    .swap-top{
      border-bottom: 1px solid rgba(42, 42, 42, 1);
      position: relative;
      text-align: center;
      padding: 20px;
      .swap-top__icon{
        position: absolute;
        left: 30px;
        top: 50%;
        transform: translateY(-50%);
        width: 24px;
        height: 19px;
        &.right{
          left: auto;
          right: 30px;
        }
      }
      .swap-info__title{
        font-size: 20px;
        color: #fff;
      }
      .swap-info__title-info{
        font-size: 14px;
        color: rgba(255, 243, 204, 1);
      }
    }
    .swap-item{
      padding: 10px 20px;
    }
